  createRoom(name:string){
    return this.client.joinOrCreate('races', {'raceId':name}).then(
      (room) => {
        this.allRooms.push(room);
        console.log(room);
        return room;
      }
    ).catch(
      (err) => console.log(err)
    );
  }

  joinRoom(roomId: string):  Promise<Room<any>>  {
    return this.client.getAvailableRooms().then(
      rooms => {
        this.allRooms = rooms as any
        this.allRooms$.next(this.allRooms);
        return this.client.joinById(roomId);
      }
    );
  }

  getRoom(roomId:string): Promise<Room<any>>{
    return this.client.getAvailableRooms().then(
      rooms => {
        this.allRooms = rooms as any
        this.allRooms$.next(this.allRooms);
        return this.allRooms.find(el => el.id == roomId);
      }
    );
  }
