import { Component, OnInit } from '@angular/core';

import { Router, ActivatedRoute, ParamMap } from '@angular/router';
import { Room, RoomAvailable } from 'colyseus.js';
import { LobbyServiceService } from 'src/app/services/lobby-service.service';

import { GameState } from '../../../../../../state/GameState';

@Component({
  selector: 'app-room',
  templateUrl: './room.component.html',
  styleUrls: ['./room.component.sass']
})
export class RoomComponent implements OnInit {
  room: RoomAvailable | any;
  state: GameState;
  formattedClockTime:string;
  currentEffort:number = 50;

  constructor(
    private route: ActivatedRoute,
    private router: Router,
    private lobbyService:LobbyServiceService
  ) { }

  ngOnInit() {
    const roomId = this.route.snapshot.paramMap.get('id');

    this.lobbyService.joinRoom(roomId).then(
      (room) => {
        this.room = room;
        room.onStateChange((newState: GameState) => {
          //Clear or do stuff here
          this.state = newState;
          this.formattedClockTime = this.formatTime(this.state.turn_time);
          console.log('State: ' + newState);
        });
      }
    );
  }

  ready(){
    this.room.send("ready", true);
  }

  formatTime(tot_seconds:number){
    const minutes = Math.floor(tot_seconds / 60);
    const seconds = ((tot_seconds % 60));
    return minutes + ":" + (seconds < 10 ? '0' : '') + seconds.toFixed(0);
  }

}
