import { Component, OnInit } from '@angular/core';
import { Router } from '@angular/router';
import { Room } from 'colyseus';
import { LobbyServiceService } from 'src/app/services/lobby-service.service';

@Component({
  selector: 'app-lobby',
  templateUrl: './lobby.component.html',
  styleUrls: ['./lobby.component.sass']
})
export class LobbyComponent implements OnInit {

  newRoomName:string='';
  rooms = [];
  stage;

  constructor(private lobbyService:LobbyServiceService, private router : Router) { }

  ngOnInit(): void {
    this.lobbyService.allRooms$.subscribe(el => {
      this.rooms = el;
      console.log(this.rooms);
    });
  }

  createRoom(){
    let ret = this.lobbyService.createRoom(this.newRoomName);
    ret.then(
      (room) => {
        console.log(room);
        this.router.navigate(['/rooms', (room as any).id  ]);
      }
    ).catch(
      err => console.error(err)
    )
    //console.log(ret);
  }

  joinRoom(room:Room){
        this.router.navigate(['/rooms', room.roomId  ]);
  }

}
